.App{
  color:#333;
  border:1px solid #aaa;
  border-radius: 4px;
  background: white;
  margin:2em;
  padding:0 1em;
  min-height:20em;
  min-width:32em;
  background: #602A41;
  -webkit-box-shadow: 0 0 1em hsla(0,0%,0%,0.15);
          box-shadow: 0 0 1em hsla(0,0%,0%,0.15);
}
.App>h2{
  color:#FEDCAC;
  margin:20px;
  position: relative;
}
.App>h2 .individuaCenter{
  position: relative;
  width:22px;
  border:2px #E44E4D solid;
}
.App>h2 .individuaCenter::before{
  content:'';
  display: block;
  position:absolute;
  margin-right:-2px;
  right:0;
  top:-11px;
  width:28px;
  border:2px #E44E4D solid;
}
.App>h2 .individuaCenter::after{
  content:'';
  display: block;
  position:absolute;
  margin-right:-2px;
  right:0;
  top:7px;
  width:28px;
  border:2px #E44E4D solid;
}
.App>h2 .individuaCenterWrapper{
  position:absolute;
  width:30px;
  height:30px;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  top:5%;
  right:0;
  cursor: pointer;
}
.App>.todoList>li:nth-child(odd){
  background: #FFFFFF;
}
.App>.todoList>li:nth-child(even){
  background: #F5F1F0;
}
.App .foreWord{
  background: #EA5050;
  color:#F7B994;
  text-align: center;
  line-height:35px;
  border-radius:3px 3px 0 0;
  font-weight:bold;
}
@media(max-width:414px){
  .App{
    min-width: 200px;
    width:100%;
    margin:0;
  }
  .App>h2 .individuaCenterWrapper{
    position:fixed;
    width:30px;
    height:30px;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    top:22px;
    right:20px;
  }
}


