.InputForm{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    margin-bottom:1em;
}
.InputForm .TodoInput{
    width:70%;
    line-height:1.8;
    font-size:16px;
    padding:0.2em;
}
.InputForm button{
    margin-left:20px;
    -ms-flex-align: middle;
        align-items: middle;
    border:none;
    outline:none;
    border-radius:3px;
    padding:10px 5px;
    font-size:20px;
    color:white;
    font-weight:bold;
    background: #EA5050;
    cursor: pointer;
}


